






import Vue from "vue";

export enum Type {
  Primary = "primary",
  Secondary = "secondary",
  Destructive = "destructive"
}

export enum Size {
  Small = "small",
  Midium = "midium",
  Large = "large"
}

export default Vue.extend({
  props: {
    nativeType: {
      type: String,
      default: "button"
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: Type.Primary
    },
    size: {
      type: String,
      default: Size.Midium
    },
    autoWidth: {
      type: Boolean,
      default: false
    },
    text: {
      type: String,
      required: true
    },
    iconName: {
      type: String,
      default: ""
    }
  },
  computed: {
    typeClassName: function() {
      return `-${this.type}`;
    },
    sizeClassName: function() {
      return `-${this.size}`;
    }
  },
  methods: {
    handleClick(): void {
      this.$emit("click");
    }
  }
});
