









import Vue from "vue";
import { IProduct } from "@/entities/Product";

export default Vue.extend({
  props: {
    product: {
      type: Object as () => IProduct,
      required: true
    }
  },
  computed: {
    productUrl(): string {
      const id = this.product ? this.product.id : "";
      return `/#/products/${id}`;
    }
  }
});
